@import "https://cdn.jsdelivr.net/npm/reset-css@5.0.1/reset.min.css";

input {
  all: unset;
  appearance: none;
  box-sizing: border-box;
}

* {
  box-sizing: border-box;
}

body {
  background-color: white;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
    Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
  font-size: 14px;
  color: white;
}

button {
  background-color: white;
  color: black;
}

a {
  text-decoration: none;
  color: inherit;
}

form {
  width: 100%;
}

.jweet-input::placeholder {
  color: white;
  opacity: 0.9;
}

.jweet_text {
  word-break: break-all;
  font-weight: 500;
  margin: 2px;
  margin-left: 15px;
  line-height: 25px;
}

.container {
  width: 100%;
  max-width: 320px;
  display: flex;
  flex-direction: column;
}

.formInput {
  width: 100%;
  padding: 8px 20px;
  border-radius: 20px;
  /* border: 1px solid gray; */
  box-shadow: 2px 2px 5px 1px gray inset;
  text-align: center;
  background-color: white;
  color: black;
}

.formBtn {
  cursor: pointer;
  width: 100%;
  padding: 7px 20px;
  text-align: center;
  color: white;
  border-radius: 20px;
  background-color: #ed9289;
  cursor: pointer;
  box-shadow: -3px -3px 3px 1px #e37e75 inset;
}

.cancelBtn {
  cursor: pointer;
  background-color: tomato;
  box-shadow: -3px -3px 3px 1px #eb4f15 inset;
}

/* Profile Page */

.profileForm {
  border-bottom: 2px solid rgb(140, 140, 140, 0.5);
  padding-bottom: 20px;
  width: 100%;
  display: flex;
  flex-direction: column;
}

.logOut {
  margin-top: 20px;
}

/* Nwitter Factory */

.factoryForm {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
}

.factoryInput__container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
  position: relative;
  margin-bottom: 20px;
  width: 100%;
}

.factoryInput__input {
  flex-grow: 1;
  height: 40px;
  padding: 0px 20px;
  color: gray;
  /* border: 1px solid #ed9289; */
  box-shadow: 2px 2px 5px 1px gray inset;
  border-radius: 20px;
  font-weight: 500;
  font-size: 12px;
}

.factoryInput__arrow {
  position: absolute;
  right: 0;
  background-color: #ed9289;
  box-shadow: -3px -3px 3px 1px #e37e75 inset;
  height: 40px;
  width: 40px;
  padding: 10px 0px;
  text-align: center;
  border-radius: 20px;
  color: white;
  box-sizing: border-box;
  cursor: pointer;
}

.factoryInput__label {
  color: #ed9289;
  cursor: pointer;
}

.factoryInput__label span {
  margin-right: 10px;
  font-size: 12px;
}

.factoryForm__attachment {
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.factoryForm__attachment img {
  height: 80px;
  width: 80px;
  border-radius: 40px;
}

.factoryForm__clear {
  color: #ed9289;
  cursor: pointer;
  text-align: center;
}

.factoryForm__clear span {
  margin-right: 10px;
  font-size: 12px;
}

/* jweet */

.jweetEdit .formBtn {
  cursor: pointer;
  margin-top: 15px;
  margin-bottom: 5px;
}

.jweet {
  box-shadow: 5px 5px 5px 3px gray;
  margin-bottom: 40px;
  background-color: white;
  width: 100%;
  max-width: 320px;
  padding: 20px;
  border-radius: 10px;
  position: relative;
  display: flex;
  flex-direction: column;
  color: rgba(0, 0, 0, 0.8);
}

.jweet h4 {
  font-size: 14px;
}

.jweet img {
  top: -45px;
  left: -42px;
  position: absolute;
  border-radius: 50%;
  width: 80px;
  height: 80px;
  margin-top: 10px;
}

.jweet__actions {
  position: absolute;
  right: 10px;
  top: 10px;
}

.jweet__actions span {
  cursor: pointer;
}

.jweet__actions span:first-child {
  margin-right: 10px;
}

/* Auth */

.authContainer {
  display: flex;
  flex-direction: column;
  height: 100vh;
  justify-content: center;
  align-items: center;
}

.authBtns {
  display: flex;
  justify-content: space-between;
  width: 100%;
  max-width: 320px;
}

.authBtn {
  cursor: pointer;
  border-radius: 20px;
  border: none;
  padding: 10px 0px;
  font-size: 12px;
  text-align: center;
  width: 150px;
  cursor: pointer;
  color: white;
}

.googleBtn {
  background-color: #e94235;
  box-shadow: -2px -2px 3px 2px #992e21 inset;
}

.githubBtn {
  background-color: #24292f;
  box-shadow: -2px -2px 3px 2px #100b11 inset;
}

/* AuthForm */

.authInput {
  max-width: 320px;
  width: 100%;
  padding: 10px;
  border-radius: 30px;
  /* border: solid 2px gray; */
  background-color: transparent;
  margin-bottom: 10px;
  font-size: 12px;
  color: black;
  box-sizing: content-box;
  box-shadow: 2px 2px 4px 1px gray inset;
}

.authSubmit {
  text-align: center;
  background: #ed9289;
  color: white;
  border: 0;
  margin-top: 10px;
  box-sizing: content-box;
  cursor: pointer;
  box-shadow: -3px -3px 3px 1px #e37e75 inset;
}

.authError {
  color: tomato;
  text-align: center;
  font-weight: 500;
  font-size: 12px;
}

.authSwitch {
  color: #ed9289;
  cursor: pointer;
  margin-top: 10px;
  margin-bottom: 50px;
  display: block;
  font-size: 12px;
  text-decoration: underline;
}

.logo_text1 {
  position: relative;
  font-weight: 600;
  font-size: 30px;
  top: 55px;
  left: 35px;
  color: #ed9289;
}

.logo_text2 {
  position: relative;
  font-weight: 600;
  font-size: 22px;
  top: 21.5px;
  left: 40px;
  color: #ed9289;
}
